fieldset.carousel {
  border: none; }
  fieldset.carousel::-webkit-scrollbar {
    width: 8px;
    height: 8px; }
  fieldset.carousel::-webkit-scrollbar-button:end:increment, fieldset.carousel::-webkit-scrollbar-button:start:decrement, fieldset.carousel::-webkit-scrollbar-button:vertical:end:decrement, fieldset.carousel::-webkit-scrollbar-button:vertical:start:increment {
    display: none; }
  fieldset.carousel::-webkit-scrollbar-track-piece:vertical {
    background-color: transparent; }
  fieldset.carousel::-webkit-scrollbar-track-piece:vertical:start {
    border-radius: 10px 10px 0 0;
    background-color: transparent; }
  fieldset.carousel::-webkit-scrollbar-track-piece:vertical:end {
    border-radius: 0 0 10px 10px;
    background-color: transparent; }
  fieldset.carousel::-webkit-scrollbar-thumb:vertical {
    height: 56px;
    border-radius: 10px;
    background-color: #494949; }

@media (max-width: 1000px), (max-height: 700px) {
  fieldset.carousel {
    padding-left: 0;
  }
}

.carousel-labels li {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  border-radius: 5px; }

.carousel-labels li p {
  margin: 0;
  text-align: center; }

.carousel {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25em;
  box-sizing: border-box;
  margin: auto; }
  .carousel ul {
    margin: 0;
    padding: 0; }
  .carousel div {
    display: flex;
    align-items: center; }
    .carousel div ul {
      display: flex;
      align-items: stretch;
      width: 20em;
      overflow: hidden; }
    .carousel div li {
      border: none;
      justify-content: center;
      flex: 0 0 20em;
      transition: left 200ms;
      position: relative;
      left: 0; }
    .carousel div .left0 li {
      left: 0em; }
    .carousel div .left1 li {
      left: -20em; }
    .carousel div .left2 li {
      left: -40em; }
    .carousel div .left3 li {
      left: -60em; }
    .carousel div .left4 li {
      left: -80em; }
    .carousel div .left5 li {
      left: -100em; }
    .carousel div .left6 li {
      left: -120em; }
    .carousel div .left7 li {
      left: -140em; }
    .carousel div .left8 li {
      left: -160em; }
    .carousel div .left9 li {
      left: -180em; }
    .carousel div .left10 li {
      left: -200em; }
    .carousel div .left11 li {
      left: -220em; }
    .carousel div .left12 li {
      left: -240em; }
    .carousel div .left13 li {
      left: -260em; }
    .carousel div .left14 li {
      left: -280em; }
    .carousel div .left15 li {
      left: -300em; }
    .carousel div .left16 li {
      left: -320em; }
    .carousel div .left17 li {
      left: -340em; }
    .carousel div .left18 li {
      left: -360em; }
    .carousel div .left19 li {
      left: -380em; }

/*/ RADIO BUTTONS /*/
.carousel-radios {
  display: flex;
  padding-top: 20px;
  justify-content: center;
  list-style-type: none;
  margin: auto; }
  .carousel-radios li {
    flex: 0 1 20px;
    text-align: center;
    position: relative; }
    .carousel-radios li input {
      cursor: pointer;
      display: block;
      height: 10px;
      width: 10px;
      z-index: 2; }
      .carousel-radios li input:focus {
        background-color: black; }
    .carousel-radios li span {
      position: absolute;
      left: 3px;
      top: 3px;
      display: block;
      width: 10px;
      height: 10px;
      border: 1.5px solid blue;
      border-radius: 50%;
      z-index: 1; }
    .carousel-radios li :checked + span {
      background-color: $primaryPink; }
    .carousel-radios li :focus + span {
      outline: 1px dashed $primaryPink; }

.forward,
.backward {
  background: none;
  border: none;
  color: $primaryPink;
  width: 20px;
  height: 20px;
  border: 3px solid;
  border-style: none none solid solid;
  transform: rotate(45deg);
  cursor: pointer;
  box-sizing: border-box;
  border-radius: 5px;
  transition: 0.5s;}

.forward:hover,
.backward:hover,
.forward:focus,
.backward:focus {
  color: $complementGreen;
  outline: none;
  box-shadow: none;
  border-width: 3.5px; }

.forward {
  border-style: solid solid none none; }

.cradio,
legend {
  position: absolute;
  opacity: 0.01; }

label {
  display: flex;
  flex-direction: column;
  align-content: center; }

