#FriendlyBase,
#enemyBase {
  position: relative;
  background: $darkGrey;
  height: 300px;
  width: 120px;
  @media (max-width: $breakWidth), (max-height: $breakHeight) {
    height: 250px;
    width: 95px;
  }
  padding: 5px;
  margin: 5px;
  margin-top: 4em;
  font-size: 0.9em;
  & h3 {
    margin: 1em 0;
  }
  z-index: -11;
}

#enemyBaseCard {
  font-size: 0.9rem;
  text-align: right;
  font-size: 0.8rem;
  &.targeted {
    @include enemyTargeted;
  }
}

#FriendlyBase {
  float: left;
  border-right: 4px solid $secondaryBlue;
  border-top: 2px solid $secondaryBlue;
  border-bottom: 5px solid $secondaryBlue;
  border-radius: 0 3em 3em 0;
  margin-left: 0px;
  &.targeted {
    @include friendlyTargeted;
  }
}

#enemyBase {
  float: right;
  text-align: right;
  border-left: 4px solid $secondaryOrange;
  border-top: 2px solid $secondaryOrange;
  border-bottom: 5px solid $secondaryOrange;
  border-right: none;
  border-radius: 3em 0 0 3em;
  margin-right: 0;
}
